@import "src/assets/styles/global";

.wrapper {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  background: #000000;
  background: rgba(0, 0, 0, 0.59);
  backdrop-filter: blur(3px);
  z-index: 999;

  .container {
    width: 1366px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      width: 320px;
      padding: 10px 0;
      cursor: pointer;

      img {
        width: 100%;
      }
    }

    .navmenu {
      display: flex;

      a {
        position: relative;
        text-align: center;
        margin-right: 24px;
        font-family: $Gotham;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 19px;
        color: #FFFFFF;
        cursor: pointer;
        transition: .3s;

        &:last-child {
          margin-right: 0;
        }

        &::before {
          display: block;
          content: attr(name);
          font-weight: bold;
          height: 0;
          overflow: hidden;
          visibility: hidden;
        }

        &:after {
          content: "";
          position: absolute;
          bottom: -4px;
          left: 0;
          height: 2px;
          width: 100%;
          background: #00FFE0;
          border-radius: 6px;
          opacity: 0;
          transition: .3s;
        }

        &:hover {
          font-weight: bold;
        }
      }

      .active {
        &:after {
          opacity: 1;
        }
      }
    }
  }
}

.navmenuMobile,
.humburgerMenu,
.closeWrap {
  display: none;
}

@media screen and (max-width: $Laptop) {
  .wrapper {

    .container {
      width: (1366px/$val);

      .logo {
        width: (320px/$val);
        padding: (10px/$val) 0;
      }

      .navmenu {

        a {
          margin-right: (24px/$val);
          font-size: (18px/$val);
          line-height: (19px/$val);

          &:after {
            bottom: (-4px/$val);
            height: (2px/$val);
          }
        }
      }
    }
  }
}

@media screen and (max-width: $Tablet) {
  .wrapper {

    .container {
      width: (1366px/$val2);

      .logo {
        width: (320px/$val2);
        padding: (10px/$val2) 0;
      }

      .navmenu {

        a {
          margin-right: (24px/$val2);
          font-size: (18px/$val2);
          line-height: (19px/$val2);

          &:after {
            bottom: (-4px/$val2);
            height: (2px/$val2);
          }
        }
      }
    }
  }
}

@media screen and (max-width: $Mobile) {
  .wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .container {
      width: 100%;
      padding: 0 16px;

      .logo {
        width: 80px;
        padding: 8px 0;
        cursor: pointer;

        img {
          width: 100%;
        }
      }

      .navmenu {
        display: none;
      }
    }

    .humburgerMenu {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;
      width: 24px;
      height: 18px;

      div {
        height: 4px;
        width: 100%;
        background: #ffffff;
        transition: .3s;
        border-radius: 4px;
      }
    }

    .humburgerMenu.open {
      div {

        &:nth-child(1) {
          transform: rotate(-45deg) translate(-5px, 5px);
          width: 24px;
        }

        &:nth-child(2) {
          opacity: 0;
        }

        &:nth-child(3) {
          transform: rotate(45deg) translate(-5px, -5px);
          width: 24px;
        }
      }
    }

    .navmenuMobile {
      transition: height .3s;
      height: 0;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      align-items: center;

      a {
        position: relative;
        margin: 16px 0;
        font-family: $Gotham;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;

        &:after {
          content: "";
          position: absolute;
          bottom: -4px;
          left: 0;
          height: 2px;
          width: 100%;
          background: #00FFE0;
          border-radius: 6px;
          opacity: 0;
          transition: .3s;
        }
      }

      a.active {
        &:after {
          opacity: 1;
        }
      }
    }

    .navmenuMobile.open {
      height: 330px;
    }
  }

  .closeWrap.open {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 998;
  }
}
